

.testimonial{
    background-color: $green;
    font-size: 2.4rem;
    padding: 2rem;
    color: $white;
    border-radius: 2rem;

    blockquote{
    
        padding-left: 5rem;
        position: relative;
    

    &::before{
        content: "";
        background-image: url(../img/quote.svg);
        background-repeat: no-repeat;
        width: 6rem;
        height: 8rem;
        display: block;
        position: absolute;
        left: -2rem;
      }
    }

    p{
        text-align: right;
        color: $white
    }
}