.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5, .dark-mode h6 {
  color: #e1e1e1;
}
.dark-mode .ad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .ad .price {
  color: #71B100;
}
.dark-mode .ad picture img {
  color: #FFFFFF;
}
.dark-mode .icons-features li img {
  filter: invert(100%);
}
.dark-mode .property-summary .price {
  color: #71B100;
}
.dark-mode .form label, .dark-mode .form legend, .dark-mode .form p {
  color: #FFFFFF;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
