  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegation {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out;
  height: 0;
}
@media (min-width: 768px) {
  .navegation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    height: auto;
  }
}
.navegation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegation a {
    margin-right: 2rem;
  }
  .navegation a:last-of-type {
    margin-right: 0;
  }
}

.header-container {
  display: flex;
