


.low-section{

    @include tablet{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}


.blog-entrance{
    margin-bottom: 2rem;

    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

}


.meta-information{
    span{
        color: $yellow; 
     }
}

.text-entrance{
    
    .a{
        display: block;
        margin-top: 2rem;
        @include tablet{
            margin-top: 0;
        }

       
    }

    h4{
        margin: 0;
        line-height: 1.4;

        &::after{
            content: "";
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $green;
            margin-top: 1rem;
        }
    }


}